const mixins = {
	data() {
		return {

		}
	},
	created() {

	},
	methods: {
		// 
		previewImg(imageList, index) {
			let imgsArray = [];

			if (Array.isArray(imageList)) {
				imgsArray = imageList
			} else {
				imgsArray.push(imageList)
			}

			uni.previewImage({
				current: index,
				urls: imgsArray,
				// loop: true
			});
		},
		// 
		Gback() {
			uni.navigateBack({
				delta: 1
			})
		},
	}
}
export default mixins;
